@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  font-family: 'Open Sans', sans-serif;
  background-color:#EFEFEF;

}

.choice-container{
  justify-content: center;
  align-items: center;
  font-size: 35px;
  line-height: 25px;

  height:100vh;
}

.choice-button {
  padding:5px 180px;
  margin:0px 10px;
  border-radius: 5px;
  font-size: 17px;
  background-color: #f3c1c6;
  border:0px;
  color:rgba(0,0,0,0.8);
  -webkit-transition-property: background-color, color;
  transition-property: background-color, color;
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
  -webkit-transition-timing-function: linear;
          transition-timing-function: linear;
  

}

.return {
  padding: 0;
}

.choice-button:hover{
  background-color: #f3c1c691;
  color:rgba(255,255,255,0.8);
  border:0px;

}
h2{
  text-align: center;
  color:rgba(0,0,0,0.8);
}

.flex-column{
  display: flex;
  flex-direction: column
}

.flex-flow{
  display: flex;
}

button {
  cursor: pointer;
}

button:focus{
  outline:none;
  
}

button.return{
  position: relative;
  top:100px;
  width:100px;
  margin:auto;
  text-align: center
}
.keypad-container{
    width:375px;
    display: flex;
    flex-direction: column;
    margin: auto;
    font-size: 20px;
}

h4{
    margin-bottom: 100px;
    text-decoration: underline;
    text-align: center;
    font-weight: 100;
    color:#707070
}

.message{
    position: absolute;
    top:0px;
    font-size: 10px;
    color:#F57E7E;
    opacity:0;
    -webkit-transition-property: all;
    transition-property: all;
    -webkit-transition-duration: 0.2s;
            transition-duration: 0.2s;
    -webkit-transition-timing-function: ease-in-out;
            transition-timing-function: ease-in-out
}

.message-enter{
    opacity: 0;
    top:0px
}

.message-enter-active{
    opacity: 1;
    top:-40px

}

.message-enter-done{
    opacity: 1;
    top:-40px

}

.input-container{
    height:50px;
    margin-bottom: 20px;
    position: relative;
}

input{
    width:100%;
    height: 100%;
    justify-content: center;
    padding:0;
    outline: none;
    text-align: center;
    margin:0;
    font-size: 20px;
    background-color: white;
    border:1px solid #DCDCDC;

    border-radius:5px;

}

ul.numbers-container{
    padding:0;
    list-style: none;
    display: flex;
    margin:auto;
    width:375px;
    flex-wrap: wrap;
    font-family: 'Open Sans', sans-serif;
    box-sizing:border-box;
    
}

.li-container{
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    width:25%;
    border:3px solid #EFEFEF;
    height:47px;
    border-radius: 7px;
    background-color:rgba(255, 255, 255, 1);
    cursor: pointer;
    font-size: 25px;
}

#focus{
    background-color:#9CD0C9
}

li.empty{
    background-color: #E9E9E9;
    cursor: auto;
}


.button-container{
    justify-content: center,

}

.button-container button{
    padding:20px 0px;
    margin-top:10px;
    border:none;
    border-radius: 5px;
    font-size: 20px;
    color:#FFFFFF
}

.button-container button:first-child{
    width:80%;
    background-color: #76C2B7;
}

.button-container button:first-child:focus{
    background-color: #429781;
}


.button-container button:last-child{
    width:20%;
    margin-left: 10px;
    background-color: #F2B6B6
}


.button-container button:last-child:focus{
 
    background-color: #F57E7E
}
.login-alert{
    width: 100%;
    position: absolute;
    height: 0;
    left:0;
    right:0;
    background-color:#009681;
    display: flex;
    color:rgba(255,255,255,0.8);
    justify-content: center;
    -webkit-transition:height 0.2s ease-in-out;
    transition:height 0.2s ease-in-out;
    overflow: hidden;

}


.login-alert-enter{
    height:0;
}
.login-alert-enter-active{
    height:70px;

}
.login-alert-enter-done{
    height:70px;

}

.login-alert h5{
    padding:0
}

#show{
    position: absolute;
    right:10px;
    top:15px;
    width: 25px;
    cursor:pointer;
}

.main-container {
  width: 100%;
  height: 200px;
  background-image: url(/codetest/static/media/start_201811.c36ed1ed.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  position: relative;
}

.logo-with-menu-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}
.logo-with-menu-container > img {
  position: relative;
  left: 2.75rem;
  top: 43px;
  width: 100px;
}

#icon-menu {
  position: relative;
  top: 43px;

  right: 2.75rem;
}
#icon-menu > img {
  width: 33px;
}
.main-container ul {
  position: relative;
  display: flex;
  right: 8.75rem;
  top: 43px;
  list-style: none;
  margin: 0;
  padding: 0;
  display: none;
}

.main-container li {
  margin-left: 2.81rem;
  color: white;
  font-size: 20px;
  text-shadow: 0px 2px 4px black;
  position: relative;
  cursor: pointer;
}

.main-container li > div {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  right: -15px;
  top: -5px;
  background-color: #f57e7e;
  font-size: 11px;
}

.slogan-container {
  width: 100%;
  color: black;
  padding: 10px;
}

.slogan-container h1 {
  font-size: 22px;
  margin: 0;
  font-family: "Open Sans", sans-serif;
  width: 100%;
  margin-bottom: 10px;
}

.slogan-container p {
  width: 100%;
  margin: auto;
  text-align: center;
  line-height: 30px;
}

.below-container {
  background-color: white;
  display: flex;
  position: relative;
  justify-content: center;
  text-align: center;
  padding-bottom: 58px;
  flex-wrap: wrap;
}

.below-container > div {
  margin-top: 35px;
}

.below-container > div:nth-child(2) {
  margin-left: 0;
  background-color: #f3e9e2;
}

.below-container > div:nth-child(3) {
  background-color: #d6e1ec;
}

.below-container > div:last-child {
  background-color: #dee1ec;
}

.image-container {
  overflow: hidden;
  width: 300px;
  font-size: 20px;
  text-align: left;
}
.image-container > div:last-child {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  padding-left: 25px;
  padding-right: 25px;
}
.image-container > div:last-child p {
  font-size: 14px;
  margin-top: 10px;
}

.image-container h4 {
  text-align: left;
  margin: 0;
  font-size: 17px;
  text-decoration: none;
  color: black;
}
.career-underline {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 20px;
  border-bottom: 2px solid white;
}

.image-container h2 {
  text-align: left;
  margin: 0;
  font-size: 24px;
}

.below-container > div img {
  width: 300px;
}

@media screen and (min-width: 370px) {
  .main-container {
    height: 200px;
  }
}

@media screen and (min-width: 400px) {
  .main-container {
    height: 240px;
  }

  .image-container {
    overflow: hidden;
    width: 400px;
    font-size: 20px;
    text-align: left;
  }
  .below-container > div img {
    width: 400px;
  }
}

@media screen and (min-width: 510px) {
  .main-container {
    height: 300px;
  }
  .slogan-container {
    padding: 20px;
  }

  .slogan-container h1 {
    font-size: 22px;
    margin: 0;
    font-family: "Open Sans", sans-serif;
    letter-spacing: 5px;
    width: 100%;
    margin-bottom: 10px;
  }
}

@media screen and (min-width: 700px) {
  .main-container {
    height: 350px;
  }
  .below-container > div {
    margin-left: 20px;
  }
}

@media screen and (min-width: 880px) {
  .main-container {
    height: 440px;
  }

  .below-container {
    padding-top: 20px;
  }

  .image-container h2 {
    text-align: left;
    margin: 0;
    font-size: 20px;
  }

  .image-container > div:last-child {
    padding-top: 20px;
  }

  .image-container h4 {
    margin-top: 20px;
    font-size: 15px;
  }
}

@media screen and (min-width: 1024px) {
  .main-container {
    height: 530px;
  }
  .slogan-container {
    position: absolute;
    top: -300px;
    color: white;
    width: 75%;
  }
  .slogan-container h1 {
    font-size: 35px;
    letter-spacing: 10px;
    margin-bottom: 10px;
  }

  .logo-with-menu-container > img {
    position: relative;
    left: 8.75rem;
    top: 43px;
    width: 141px;
  }
}

@media screen and (min-width: 1100px) {
  .main-container ul {
    display: flex;
  }

  #icon-menu {
    display: none;
  }
}

@media screen and (min-width: 1280px) {
  .main-container {
    height: 640px;
  }
  .slogan-container {
    top: -350px;
  }
}

@media screen and (min-width: 1366px) {
  .main-container {
    height: 700px;
  }
  .slogan-container {
    top: -400px;
  }
}

@media screen and (min-width: 1440px) {
  .main-container {
    height: 740px;
  }
  .slogan-container {
    top: -450px;
  }
}

@media screen and (min-width: 1600px) {
  .main-container {
    height: 800px;
  }

  .slogan-container {
    top: -500px;
  }
}

@media screen and (min-width: 1680px) {
  .main-container {
    height: 840px;
  }
}

@media screen and (min-width: 1920px) {
  .main-container {
    height: 1000px;
  }
  .slogan-container {
    top: -600px;
  }
}

